import React from 'react';
import cn from 'classnames';

type Props = {
  currentPage: number;
  setCurrentPage: (page: number) => void;
  pageQuantity: number;
};

export const Pagination: React.FC<Props> = ({
  currentPage,
  setCurrentPage,
  pageQuantity,
}) => {
  const result: number[] = [];

  for (let i = 1; i <= pageQuantity; i += 1) {
    result.push(i);
  }

  return (
    <ul className="pagination">
      <li className={cn('page-item', { disabled: currentPage === 1 })}>
        <a
          data-cy="prevLink"
          className="page-link"
          href="#prev"
          aria-disabled="true"
          onClick={() => {
            if (currentPage !== 1) {
              setCurrentPage(currentPage - 1);
            }
          }}
        >
          «
        </a>
      </li>

      {result.map(page => (
        <li
          className={cn('page-item', { active: page === currentPage })}
          key={page}
        >
          <a
            data-cy="pageLink"
            className="page-link"
            href="#1"
            onClick={() => setCurrentPage(page)}
          >
            {page}
          </a>
        </li>
      ))}

      <li className="page-item">
        <a
          data-cy="nextLink"
          className="page-link"
          href="#next"
          aria-disabled="false"
          onClick={() => {
            if (currentPage < pageQuantity) {
              setCurrentPage(currentPage + 1);
            }
          }}
        >
          »
        </a>
      </li>
    </ul>
  );
};
