import React, { useState } from 'react';
import './App.css';
import { getNumbers } from './utils';
import { Pagination } from './components/Pagination/Pagination';

const items = getNumbers(1, 42).map(n => `Item ${n}`);
const itemsLength = items.length;

export const App: React.FC = () => {
  const [itemsPerPage, setItemsPerPage] = useState(3);
  const [currentPage, setCurrentPage] = useState(2);
  const first = (currentPage - 1) * itemsPerPage + 1;
  const last = first + itemsPerPage;
  // const visibleItems = items.slice()

  const pageQuantity = Math.ceil(itemsLength / itemsPerPage);
  // const itemsOnLastPage = itemsLength - (pageQuantity - 1) * itemsPerPage;

  return (
    <div className="container">
      <h1>Items with Pagination</h1>

      <p className="lead" data-cy="info">
        Page {currentPage} (items {first} - {last} of {itemsLength})
      </p>

      <div className="form-group row">
        <div className="col-3 col-sm-2 col-xl-1">
          <select
            className="form-control"
            data-cy="perPageSelector"
            id="perPageSelector"
            value={itemsPerPage}
            onChange={(event: React.ChangeEvent<HTMLSelectElement>) =>
              setItemsPerPage(Number(event.target.value))
            }
          >
            <option value="3">3</option>
            <option value="5">5</option>
            <option value="10">10</option>
            <option value="20">20</option>
          </select>
        </div>

        <label htmlFor="perPageSelector" className="col-form-label col">
          items per page
        </label>
      </div>

      {/* Move this markup to Pagination */}
      <Pagination
        currentPage={currentPage}
        setCurrentPage={setCurrentPage}
        pageQuantity={pageQuantity}
      />

      <ul>
        {items.map(item => (
          <li data-cy="item" key={item}>
            {item}
          </li>
        ))}
      </ul>
    </div>
  );
};

export default App;
